export default {
	data() {
		return {
			timeout:"",
		}
	},
	onReady() {
		// this.backHome();
	},
	onLoad() {
		this.backHome();
	},
	methods: {
		//路由统一事件
		godetail: function(e) {
			clearTimeout(timeout)
			if(e==0){
				uni.switchTab({
					url: '../order/OrderList'
				});
			}else if(e==1){
				uni.switchTab({
					url: '/pages/Home/Index'
				});
			}
		},
		backHome : function() {
			this.timeout=setTimeout(() => { 
				uni.switchTab({
					url: '/pages/Home/Index'
				});
			}, 10000)
		}

	}
}
